<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <depSeach v-if="popShow" :name="curName" @changepop="popClick"></depSeach>
    </transition>
    <div class="content">
      <div class="c-top">
        <div class="c-t-left">
          <ctitle :title="'工程过程监控'"></ctitle>
          <div class="gcjk">
            <gcgcjkDiv></gcgcjkDiv>
          </div>
        </div>
        <div class='c-t-right'>
          <ctitle :title="'市级信访部门排名TOP10'"></ctitle>
          <div class="w-charts">
            <div class='btn'>
              <blzlBtn @change="changeBar"></blzlBtn>
            </div>
            <div class='chart'>
              <barline :params="xflphBarParams"></barline>
            </div>
          </div>
        </div>
      </div>
      <div class="c-bottom">
        <div class="w-charts">
          <ctitle :title="'参评率'"></ctitle>
          <div class="charts">
            <meiguiPie :params="cplParams"></meiguiPie>
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'满意率'"></ctitle>
          <div class="charts">
            <meiguiPie :params="mylParams"></meiguiPie>
          </div>
        </div>
        <div class="w-charts">
          <ctitle :title="'及时受理率'"></ctitle>
          <div class="charts">
            <meiguiPie :params="jssllParams"></meiguiPie>
          </div>
        </div>
      </div>
      <!-- <div class="c-bottom">
        <ctitle :title="'市级信访部门排名TOP10'"></ctitle>
        <blzlBtn @change="changeBar"></blzlBtn>
        <div class="w-echart" id="w-echart" ref="barEl">
          <PaimBar :params="zrdwBarParams"></PaimBar>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import { get_http } from "@/api/index";
import ctitle from "@/components/title/index";
import meiguiPie from "@/components/pie/meiguiPie";
import gcgcjkDiv from "@/components/gcgcjk/index";
import blzlBtn from "@/components/qhpmBtn/xfbmBtn";
import PaimBar from "@/components/bar/PaimBar";
import depSeach from "@/components/depSeach/index";
import barline from "@/components/bar/barLine2";

export default {
  name: "lfzrdw",
  components: {
    ctitle,
    gcgcjkDiv,
    meiguiPie,
    blzlBtn,
    PaimBar,
    depSeach,
    barline
  },
  data() {
    return {
      // 责任单位排名
      zrdwBarBtn:'办理量',
      xflphBarParams: {
        data: [],
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        options: {
          // isVertical:true,
          xName: "name", //x轴字段
          axisValueShow: false,
          categoryBr: true,
          axis1Name: "人次",
          axisLabelSize: 14, // label字号
          left: "0%",
          right: "5%",
          top: "5%",
          bottom: "0%",
          barLabelOptions: "top",
          barWidth: "25%",
          barBorderRadius: 10,
          splitLineShow: false,
          barLabelShow: false, //柱状图数量值显示
          axisLabelColor: "rgba(0, 176, 255, .8)",
          itemLabelColor: "rgba(0, 176, 255, .8)",
          colors: {
            bar: [["#7EE0FF", "#036CF5"]]
          },
          map: {
            value: "数量"
          }
        }
      },
      // 网信占比
      xfxs:'',
      xfjc:'',
      xfbm:'',
      zbRingParams: {
        wxbfb:'', //网信占比标题百分比
        wxbfbAll:'', //网信占比标题总数
        canvasStyle: {
          width: '100%',
          height: '100%'
        },
        options: {
          colors:['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
          unti: '件',
          labelColor: '#fff',
          labelFontSize: 16, //21
          labelLineSmooth: 0,
          labelLineHeight: 23
        },
        data: [
            {value: 70, name: '已评价'},
            {value: 12, name: '超期未评价'},
            {value: 18, name: '待评价'}
        ].sort(function (a, b) { return a.value - b.value; }),
      },
      // 参评率
      name_ypjjc:'已评价,超期未评价',
      ypjjc:'',
      cplParams: {
        canvasStyle: {
          width: '100%',
          height: '100%'
        },
        options: {
          colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          unti: '件',
          labelColor: '#fff',
          labelFontSize: 16, //21
          labelLineSmooth: 0,
          labelLineHeight: 23
        },
        data: [
            {value: 70, name: '已评价'},
            {value: 12, name: '超期未评价'},
            {value: 18, name: '待评价'}
        ].sort(function (a, b) { return a.value - b.value; }),
      },
      // 满意率
      name_myjc:'满意,不满意,基本满意',
      myjc:'',
      mylParams: {
        canvasStyle: {
          width: '100%',
          height: '100%'
        },
        options: {
          colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          unti: '件',
          labelColor: '#fff',
          labelFontSize: 16, //21
          labelLineSmooth: 0,
          labelLineHeight: 23
        },
        data: [
            {value: 70, name: '已评价'},
            {value: 12, name: '超期未评价'},
            {value: 18, name: '待评价'}
        ].sort(function (a, b) { return a.value - b.value; }),
      },
      // 及时受理率
      name_jssljc:'及时受理,超期受理,超期未受理',
      jssljc:'',
      jssllParams: {
        canvasStyle: {
          width: '100%',
          height: '100%'
        },
        options: {
          colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
          unti: '件',
          labelColor: '#fff',
          labelFontSize: 16, //21
          labelLineSmooth: 0,
          labelLineHeight: 23
        },
        data: [
            {value: 70, name: '已评价'},
            {value: 12, name: '超期未评价'},
            {value: 18, name: '待评价'}
        ].sort(function (a, b) { return a.value - b.value; }),
      },
      loadFlag: false,
      popShow: false,
      curTypeId: "drop",
      curName: "部门索引",
      // 各市切换列表
      menuShow: false,
      menuIndex: 0,
      menuName: "石家庄市",
      menuArr: [
        "石家庄市",
        "张家口市",
        "保定市",
        "秦皇岛市",
        "廊坊市",
        "衡水市",
        "邯郸市",
        "承德市",
        "邢台市",
        "唐山市",
        "沧州市",
        "定州市",
        "辛集市",
        "雄安新区"
      ],
      
      // 四级登记机构占比
      djjgPieParams: {
        canvasStyle: {
          width: "100%",
          height: "100%"
        },
        data: [],
        options: {
          colors: this.$STANDARD.gradientColors,
          radius: ["60%", "75%"],
          center: ["50%", "50%"],
          centerRadius: ["52%", "53%"],
          labelRich: {
            b: this.fontSize(0.2),
            d: this.fontSize(0.22),
            bHeight: this.fontSize(0.3)
          },
          length: 20,
          length2: 45,
          borderWidth: this.fontSize(0.05),
          bRichPadding: [30, -45, 0, -45],
          tooltip: {
            show: true
          },
          itemLabelShow: true, //数据扇区label是否显示
          //图例
          legend: {
            show: false
          },
          rich: {
            bfontsize: "14%" //文字大小
          },
          labelSize: "22%", // label字号
          rollFlag: true
        }
      },
      
    };
  },
  mounted() {
    this.init(this.curTypeId,this.curName,this.date)
  },
  methods: {
    // 初始化组件
    init(curTypeId,curName,date){
      // let xfbmParam=(xfbm === '部门索引' ? 'drop' : xfbm);
      this.getAPI(curTypeId,curName,date);
      this.getCenterAPI(curTypeId,curName,date);
    },
    // 点击部门索引触发
    handlePopShow() {
      this.popShow = true;
    },
    // 点击弹框确认按钮后触发
    popClick(name){
      this.curName=(name === ''?'部门索引':name);
      this.getAPI(this.curTypeId,this.curName,this.date);
      this.getCenterAPI(this.curTypeId,this.curName,this.date)
    },
    // 各市列表切换
    menuChange(index) {
      this.menuIndex = index;
      this.menuName = this.menuArr[this.menuIndex];
      this.menuShow = false;
      this.getAPI(this.curTypeId,this.date,this.menuName);
      this.getCenterAPI(this.curTypeId,this.date,this.menuName);
    },
    // 市级信访部门排名TOP10按钮
    changeBar(id) {
      this.zrdwBarBtn = id;
      this.getCenterAPI(this.curTypeId,this.curName,this.date,this.zrdwBarBtn);
    },
    getAPI(curTypeId,curName,date) {
      // 2.网信占比
      get_http("xfjxkhxfbmwxzb",{ 
        name:this.xfxs,
        value:this.xfjc,
        xfbm:curName,
        day: `[${date.startDate},${date.endDate}]`,
       })
        .then(res => {
          this.zbRingParams.data = res.data.data;
          this.zbRingParams.wxbfbAll = parseInt(this.zbRingParams.data[0].value)+parseInt(this.zbRingParams.data[1].value)+parseInt(this.zbRingParams.data[2].value);
          this.zbRingParams.data.forEach((item) => {
            if(item.name == '网信'){
              this.zbRingParams.wxbfb = Math.round((item.value/this.zbRingParams.wxbfbAll)*100 * 100) / 100
            }
          });
        })
        .catch(err => {
          console.log(err);
        });
      // 3.参评率
      get_http("xfjxkhxfbmcpl",{ 
        main_name:this.name_ypjjc,
        value:this.ypjjc,
        xfbm:curName,
        xfxs:"来访",
        day: `[${date.startDate},${date.endDate}]`,
       })
        .then(res => {
          this.cplParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
      // 4.满意率
      get_http("xfjxkhxfbmmyl",{ 
        main_name:this.name_myjc,
        value:this.myjc,
        xfxs:"来访",
        xfbm:curName,
        day: `[${date.startDate},${date.endDate}]`,
       })
        .then(res => {
          this.mylParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        }); 
      // 5.及时受理率
      get_http("xfjxkhxfbmjssll",{
        main_name:this.name_jssljc,
        value:this.jssljc,
        xfxs:"来访",
        xfbm:curName,
        day: `[${date.startDate},${date.endDate}]`,
       })
        .then(res => {
          
          this.jssllParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        }); 
      
    },
    // 6.责任单位排名TOP10柱图
    getCenterAPI(curTypeId,curName,date){
      // get_http("xfjxkhxfbmzrdwpm", {
      //   id:curTypeId,
      //   xfbm:curName,
      //   day: `[${date.startDate},${date.endDate}]`,
      //   // zrdwBarBtn:this.zrdwBarBtn
      //   })
      //   .then(res => {
      //     this.zrdwBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
      //     if (this.zrdwBarParams.data.length > 14) {
      //       this.computation();
      //     }
      //   })
      //   .catch(err => {
      //     console.log(err);
      //   });
      if (this.curCity === '广东省') {
        this.shej_02 = this.curCity;
        this.shij_02 = 'invalid';
        this.xj_02 = 'invalid';
        this.nameP = 'shij_02';
      } else {
        // 市级
        if (this.shijCity) {
          this.shej_02 = "广东省";
          this.shij_02 = this.shijCity;
          this.nameP = "xj_02";
          if (this.xj_02 !== 'invalid') {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
          } else {
            this.shej_02 = "广东省";
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
          }
        } else {
          this.shej_02 = "广东省";
          this.shijCity = 'invalid',
          this.xj_02 = 'invalid'
          this.nameP = "invalid";
        }
      }
      get_http("xfjxkhzrdwsjxfbmpm", {
        shej_02: this.shej_02,
        shij_02: this.shij_02,
        xj_02: this.xj_02,
        name: this.nameP,
        day: `[${date.startDate},${date.endDate}]`,
        id:this.curTypeId
      })
        .then(res => {
          this.xflphBarParams.data = res.data.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    
    
    fontSize(res) {
      let docEl = document.documentElement,
        clientWidth =
          window.innerWidth ||
          document.documentElement.clientWidth ||
          document.body.clientWidth;
      if (!clientWidth) return;
      let fontSize = 100 * (clientWidth / 1920);
      return res * fontSize;
    }
  },
  computed: {
    date() {
      return this.$store.state.date;
    }
  },
  watch: {
    date(val) {
      this.getAPI(val);
      this.getCenterAPI(val)
    }
  }
};
</script>

<style lang="stylus" scoped>
.wrapper 
  position relative
  top 0.8rem
  height calc(100% - 0.8rem)

  .fade-enter-active, .fade-leave-active 
    transition opacity 0.3s

  .fade-enter, .fade-leave-to 
    opacity 0

  .w-seach 
    position absolute
    top -78px
    right 113px
    z-index 1000
    box-sizing border-box
    padding-left 42px
    width 262px
    line-height 62px
    font-size 22px
    color #cbfbff
    background url('../../../assets/search-bg2.png') no-repeat
    background-size 100% 100%
    cursor pointer

    p 
      width 150px
      text-align center
      white-space nowrap
      overflow hidden
      text-overflow ellipsis

  

  .content 
    width 100%
    height 100%
    display flex
    padding 0.2rem 0.3rem
    flex-direction column
    box-sizing border-box
    .c-top
      flex 1
      position relative
      box-sizing border-box
      display flex
      .c-t-left
        flex 1
        box-sizing border-box
        .gcjk 
          position relative
      .c-t-right
        flex 1
        box-sizing border-box
        display flex
        flex-direction column
        .w-charts
          flex 1 
          display flex
          flex-direction row
          .btn
            flex .2
            display flex
            height 100%
            align-items center
            justify-content flex-end
          .chart 
            flex .8
            display flex
            height 100%
            align-items center
            justify-content flex-start
    .c-bottom
      flex 1
      display flex
      box-sizing border-box
      flex-direction row
      .w-charts 
        flex 1
        flex-direction column
        height 100%
        display flex
        .charts 
          flex 1
          display flex
          width 100%
          height 90%


  .el-loading-spinner
    display none !important  


</style>
